import React from "react";

import './assets/vendors/animate/animate.css';
import './assets/vendors/themify-icons/css/themify-icons.css';
import './assets/css/codecrush.css';

import {
  ApolloClient,
  InMemoryCache,
  ApolloProvider,
  createHttpLink,
} from '@apollo/client';
import { setContext } from '@apollo/client/link/context';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import Signup from './pages/Signup';
import Login from './pages/Login';
import Contact from './pages/Contact';
import Home from "./pages/Home";
import Head from "./components/Head";
import Footer from "./components/Footer";
import About from './pages/About';
import Learn from './pages/Learn.js';
import Reviews from './pages/Reviews';
import UserCards from './pages/UserCards/UserCards';
import SwipeButtons from './pages/SwipeButtons/SwipeButtons';
import Navbar from './components/navbar';
import HomeBody from './pages/HomeBody';

// Construct our main GraphQL API endpoint
const httpLink = createHttpLink({
  uri: '/graphql',
});


// Construct request middleware that will attach the JWT token to every request as an `authorization` header
const authLink = setContext((_, { headers }) => {
  // get the authentication token from local storage if it exists
  const token = localStorage.getItem('id_token');
  // return the headers to the context so httpLink can read them
  return {
    headers: {
      ...headers,
      authorization: token ? `Bearer ${token}` : '',
    },
  };
});

const client = new ApolloClient({
  // Set up our client to execute the `authLink` middleware prior to making the request to our GraphQL API
  link: authLink.concat(httpLink),
  cache: new InMemoryCache(),
});

function App() {
  return (

    <ApolloProvider client={client}>
      <Router>
        <Navbar />
        <Routes>
          <Route
          
           path="/"
           element={<><Home /> <HomeBody /> <Footer /> </>}
         />
          <Route
            path="/login"
            element={<><Login /><Footer /></>}
          />
          <Route
            path="/signup"
            element={<><Signup /><Footer /></>}
          />
          <Route
            path="/contact"
            element={<><Contact /><Footer /></>}
          />

          <Route
            path="/about"
            element={<><About /><Footer /></>}
          />
          <Route
            path="/Learn"
            element={<><Learn /><Footer /></>}
          />
          <Route
            path="/Reviews"
            element={<><Reviews /><Footer /></>}
          />

          <Route
            path="/home"
            element={<><Home /><Footer /></>}
          />
           <Route
            path="/UserCards"
            element={<><UserCards /><SwipeButtons /></>}
          />
          
        </Routes>
      </Router>
      
    </ApolloProvider>
  );
}

export default App;
